<template>
   <div class="p-6 bg-[#46217d] h-screen flex  flex-col items-center">
    <h2 class="font-bold mb-4 py-10 text-[3em] text-white ">Contact Me</h2>
    <form @submit.prevent="sendMessage" class="bg-[#3c176c] p-6 rounded-lg shadow-md w-[50em]">
      <div class="mb-4">
        <input
          type="text"
          v-model="name"
          placeholder="Your Name"
          required
          class="w-full p-3  bg-[#46217d] rounded-md focus:outline-none focus:ring focus:ring-blue-300"
        />
      </div>
      <div class="mb-4">
        <input
          type="email"
          v-model="email"
          placeholder="Your Email"
          required
          class="w-full p-3  bg-[#46217d] rounded-md focus:outline-none focus:ring focus:ring-blue-300"
        />
      </div>
      <div class="mb-4">
        <textarea
          v-model="message"
          placeholder="Your Message"
          required
          class="w-full p-3  bg-[#46217d] rounded-md focus:outline-none focus:ring focus:ring-blue-300"
        ></textarea>
      </div>
      <button
        type="submit"
        class="w-full bg-blue-500 text-white font-semibold py-2 rounded-md hover:bg-blue-600 transition duration-200"
      >
        Send
      </button>
    </form>
  </div>
</template>

<script>
export default {
  name: 'ContactMe',
  data() {
    return {
      name: '',
      email: '',
      message: '',
    };
  },
  methods: {
    sendMessage() {
      alert(`Message sent from ${this.name}`);
      this.name = '';
      this.email = '';
      this.message = '';
    },
  },
};
</script>

<style scoped>
