import { createRouter, createWebHistory } from 'vue-router';
import HomePage from '../views/HomePage.vue'; // Ensure this file exists
import MyProjects from '../components/MyProjects.vue'; // Ensure this file exists
import ContactMe from '../components/ContactMe.vue'; // Ensure this file exists
import AboutMe from '../components/AboutMe.vue';
import TodoApp from '@/components/TodoApp.vue';
import QouteGenerator from '@/components/QouteGenerator.vue';

const routes = [
  {
    path: '/',
    name: 'HomePage',
    component: HomePage,
  },
  {
    path: '/about',
    name: 'AboutMe',
    component: AboutMe,
  },
  {
    path: '/projects',
    name: 'MyProjects',
    component: MyProjects,
  },
  {
    path: '/contact',
    name: 'ContactMe',
    component: ContactMe,
  },
  {
    path: '/todo',
    name: 'TodoApp',
    component: TodoApp,
  },
  {
    path: '/qoute',
    name: 'QouteGenerator',
    component: QouteGenerator,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
